}
:host *,
:host *::after,
:host *::before {
  box-sizing: border-box;
}
:host ::-webkit-scrollbar-button {
  display: none;
}
@-moz-document url-prefix() {
  :host * {
    scrollbar-color: var(--theme-scrollbar-thumb--background) var(--theme-scrollbar-track--background);
    scrollbar-width: thin;
  }
}
:host ::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
}
:host ::-webkit-scrollbar-track {
  border-radius: 5px;
  background: var(--theme-scrollbar-track--background);
}
:host ::-webkit-scrollbar-track:hover {
  background: var(--theme-scrollbar-track--background--hover);
}
:host ::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: var(--theme-scrollbar-thumb--background);
}
:host ::-webkit-scrollbar-thumb:hover {
  background: var(--theme-scrollbar-thumb--background--hover);
}
:host ::-webkit-scrollbar-corner {
  display: none;
}
:host .card {
  background-color: var(--theme-menu--background);
  width: 100%;
  box-shadow: none;
  border: none;
  border-radius: unset;
  display: flex;
  flex-direction: column;
}
:host .card.standaloneAppearance {
  box-shadow: var(--theme-shadow-4);
  border-radius: 0.25rem;
}
:host .card.rounded {
  border-radius: 4px;
}
:host .card.left {
  border-radius: 4px 0 0 4px;
  height: 100%;
}
:host .card.right {
  border-radius: 0 4px 4px 0;
  height: 100%;
}
:host .card .header {
  padding: 1rem;
  flex: 0 1 auto;
}
:host .card .separator {
  border: 1px solid var(--theme-datepicker-separator--background);
  width: 100%;
  flex: 0 1 auto;
}
:host .card .content {
  padding: 0 1rem 1rem 1rem;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}